
.wrapper
{
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    .modal
    {
        width: 90vw;
        padding: 15px;
        background-color: white;
        border-radius: 5px;
        cursor: auto;
    }

}